import $ from "jquery";
function formValidate(formSelector) {
    $(formSelector).validate({
        rules: {
            name: {
                required: true,
            },
            phone: {
                required: true
            },
            email: {
                required: true,
                email: true
            },
            message: {
                required: true,
                minlength: 30
            }
        },
        messages: {
            name: "Пожалуйста, введите Ваше имя!",
            email: {
                required: "Введите Ваш e-mail!",
                email: "Введите корректный E-mail!"
            },
            phone: {
                required: "Пожалуйста, введите Ваш номер!"
            },
            message: {
                required: "Пожалуйства, введите сообщение!",
                minlength: "Пожалуйста, введите минимум 30 символов!"
            }
        }
    }
    );
}



export default formValidate;

